import { createSelector } from 'reselect';
import { STORE_KEY } from './constants';
import { State } from './reducer';
import { AppState } from 'reducers';

/**
 * Direct selector to the exportSystemForm state domain
 */
const selectExportSystemFormDomain = () => (state: AppState): State =>
  state[STORE_KEY];

/**
 * Other specific selectors
 */

/**
 * Default selector used by ExportSystemForm
 */

const makeSelectExportSystemForm = () =>
  createSelector(selectExportSystemFormDomain(), (substate) => substate);

export default makeSelectExportSystemForm;
export { selectExportSystemFormDomain };



