import React from 'react';
import styled from 'styled-components';

import { Link } from 'react-router-dom';
import { ModalBody, Col, Row } from 'reactstrap';
import { FormattedMessage } from 'react-intl';
import ExportToGitHubOption from 'components/ExportToGitHubOption';
import ExportToFsoption from 'components/ExportToFsoption';
import messages from './messages';
import { ExportSystemDestinationTypes } from 'cb-utils/console-entity-models/exportSystem';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import {
  GitHubAccessTokenKey,
  createGitHubOAuthUrl,
} from 'cb-utils/console-entity-models/github';
import {
  verifyGitHubAccessToken,
  getGitHubClientId,
} from 'cb-utils/services/github';
import LoadingIndicator from 'components/LoadingIndicator';

const RowWrapper = styled(Row)`
  width: 80%;
`;

interface IProps extends SystemRouteProps<{}> {}

interface IState {
  hasValidGitHubToken: boolean;
  gitHubClientId: string;
}

export class ExportSystemModalBody extends React.Component<IProps, IState> {
  state: IState = {
    hasValidGitHubToken: false,
    gitHubClientId: '',
  };

  fetchGitHubClientId = () => {
    getGitHubClientId().then((resp) => {
      this.setState({
        ...this.state,
        gitHubClientId: resp.client_id,
      });
    });
  };

  componentWillMount() {
    if (localStorage[GitHubAccessTokenKey]) {
      verifyGitHubAccessToken(localStorage[GitHubAccessTokenKey])
        .then(() => {
          this.setState({
            ...this.state,
            hasValidGitHubToken: true,
          });
        })
        .catch(this.fetchGitHubClientId); // if the token is invalid we need to fetch the client ID so that the user can go through OAuth process
    } else {
      // if there is no token we need to fetch the client ID so that the user can go through OAuth process
      this.fetchGitHubClientId();
    }
  }

  render() {
    return (
      <ModalBody>
        <p className="lead">
          <FormattedMessage {...messages.selectDestination} />
        </p>
        <div className="d-flex justify-content-center">
          <RowWrapper>
            <Col data-cy="github-oauth-container">
              {this.renderGitHubOption()}
            </Col>
            <Col data-cy="export-filesystem-container">
              <Link
                to={`${this.props.match.url}/${
                  ExportSystemDestinationTypes.FILE_SYSTEM
                }`}
              >
                <ExportToFsoption />
              </Link>
            </Col>
          </RowWrapper>
        </div>
      </ModalBody>
    );
  }

  renderGitHubOption = () => {
    const option = <ExportToGitHubOption />;
    const gitHubLink = `${this.props.match.url}/${
      ExportSystemDestinationTypes.GITHUB
    }`;
    if (this.state.hasValidGitHubToken) {
      return <Link to={gitHubLink}>{option}</Link>;
    } else {
      return this.state.gitHubClientId ? (
        <a
          href={createGitHubOAuthUrl(
            this.state.gitHubClientId,
            `${location.origin}${gitHubLink}`,
          )}
        >
          {option}
        </a>
      ) : (
        <LoadingIndicator />
      );
    }
  };
}



