import { platformFetch, AllowedMethods } from '../cbFetch';
import {
  IEventTotalsQuery,
  IEventTotalsResponse,
} from '../console-entity-models/analytics';

export const getEventTotals = (
  query: IEventTotalsQuery,
): Promise<IEventTotalsResponse> =>
  platformFetch(
    `/api/v/2/analytics/eventtotals?query=${encodeURIComponent(
      JSON.stringify(query),
    )}`,
    {
      method: AllowedMethods.GET,
    },
  );



