/*
 *
 * ExportSystemModal
 *
 */

import React from 'react';
import { connect } from 'react-redux';

import { Modal, ModalHeader, BreadcrumbItem, Breadcrumb } from 'reactstrap';
import { FormattedMessage } from 'react-intl';

import { compose } from 'redux';
import messages from './messages';
import { CBDispatch } from 'cb-utils/typeUtils';
import { Link, Route, matchPath } from 'react-router-dom';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import ConsoleRoutes from '../Console/routes';
import ExportSystemForm from '../ExportSystemForm';
import { history } from 'appHistory';
import { ExportSystemDestinationTypes } from 'cb-utils/console-entity-models/exportSystem';
import { ModalBreadcrumbContainer } from 'components/CbBreadcrumbContainer';
import { ExportSystemModalBody } from './ExportSystemModalBody';
import { getParentRoute } from 'cb-utils/getParentRoute';

interface MyRouteProps {
  type: ExportSystemDestinationTypes;
}

interface ExportSystemModalProps extends SystemRouteProps<MyRouteProps> {}

export class ExportSystemModal extends React.PureComponent<
  ExportSystemModalProps & DispatchToProps,
  {}
> {
  render() {
    return (
      <Modal isOpen size="lg" toggle={this.toggle}>
        <ModalHeader toggle={this.toggle}>
          <ModalBreadcrumbContainer>
            <Breadcrumb>
              {this.renderBaseCrumb()}
              {this.renderChildCrumbs()}
            </Breadcrumb>
          </ModalBreadcrumbContainer>
        </ModalHeader>

        <Route
          path={ConsoleRoutes.EXPORT_SYSTEM_MODAL_DESTINATION_TYPE}
          render={(props) => (
            <ExportSystemForm {...props} onCancel={this.toggle} />
          )}
        />
        {/* note: this is kinda weird, but basically we need to hide the landing page if we're in a subroute */}
        {!this.getExportType() && <ExportSystemModalBody {...this.props} />}
      </Modal>
    );
  }

  toggle = () => {
    this.props.onCloseModal();
  };

  getExportType = (): ExportSystemDestinationTypes => {
    let exportType;
    // note: since child route params aren't available to parent routes in react-router,
    // we need to grab the params manually
    const match = matchPath<MyRouteProps>(history.location.pathname, {
      path: ConsoleRoutes.EXPORT_SYSTEM_MODAL_DESTINATION_TYPE,
    });
    if (match && match.params.type) {
      exportType = match.params.type;
    }
    return exportType;
  };

  renderBaseCrumb = () => {
    const onParentRoute = this.props.location.pathname === this.props.match.url;
    const msg = <FormattedMessage {...messages.header} />;
    return (
      <BreadcrumbItem>
        {onParentRoute ? msg : <Link to={this.props.match.url}> {msg} </Link>}
      </BreadcrumbItem>
    );
  };

  renderChildCrumbs = () => {
    const exportType = this.getExportType();
    if (exportType) {
      let msg;
      switch (exportType) {
        case ExportSystemDestinationTypes.FILE_SYSTEM:
          msg = messages.fileSystem;
          break;
        case ExportSystemDestinationTypes.GITHUB:
          msg = messages.github;
          break;
      }
      return (
        <BreadcrumbItem>
          <FormattedMessage {...msg} />
        </BreadcrumbItem>
      );
    }
    return null;
  };
}

type DispatchToProps = ReturnType<typeof mapDispatchToProps>;
function mapDispatchToProps(
  dispatch: CBDispatch,
  ownProps: ExportSystemModalProps,
) {
  return {
    onCloseModal: () => {
      ownProps.history.push(getParentRoute(ownProps.match.url));
    },
  };
}

const withConnect = connect(null, mapDispatchToProps);

export default compose(withConnect)(ExportSystemModal);



