/*
 *
 * ExportSystemForm reducer
 *
 */

import assign from 'immutable-assign';
import { ActionTypes } from './actions';
import { ActionConstants } from './actionConstants';
import { ActionConstants as IpmConstants } from 'containers/Console/actions/ipmConstants';
import { ActionTypes as IpmActionTypes } from 'containers/Console/actions/ipm';
import { ExportSystemResponse } from 'cb-utils/console-entity-models/exportSystem';
import { IpmAssetTypes } from 'cb-utils/console-entity-models/ipm';

export interface State {
  isExporting: boolean;
  success: boolean;
  successResponse: ExportSystemResponse;
  assetTypes: IpmAssetTypes[];
}

export const initialState: State = {
  isExporting: false,
  success: false,
  successResponse: {
    zipAddress: '',
  },
  assetTypes: [],
};

function exportSystemFormReducer(
  state: State = initialState,
  action: ActionTypes | IpmActionTypes,
) {
  switch (action.type) {
    case ActionConstants.INIT_EXPORT_SYSTEM_FORM_SUCCESS:
      return assign(
        state,
        (s) => s.assetTypes,
        () => action.payload.assetTypes,
      );
    case ActionConstants.ON_ASSET_TYPE_CHANGE:
      return assign(
        state,
        (s) => s.assetTypes,
        () => action.payload.assetTypes,
      );
    case ActionConstants.EXPORT_SYSTEM_REQUEST:
      return assign(state, (s) => s.isExporting, () => true);
    case ActionConstants.EXPORT_SYSTEM_SUCCESS:
      return assign(state, (s) => {
        return {
          ...s,
          isExporting: false,
          success: true,
          successResponse: action.payload.data,
        };
      });
    case ActionConstants.EXPORT_SYSTEM_ERROR:
      return assign(state, (s) => ({
        ...s,
        isExporting: false,
      }));
    case IpmConstants.CLEAN_UP_EXPORT_SYSTEM:
      return assign(state, () => initialState);
    default:
      return state;
  }
}

export default exportSystemFormReducer;



