/**
 *
 * ExportToGitHubOption
 *
 */

import React from 'react';
// import styled from 'styled-components';
import messages from './messages';
import myImg from 'img/github-logo.png';
import ExportOption from '../ExportOption';

class ExportToGitHubOption extends React.Component<{}, {}> {
  render() {
    return (
      <ExportOption
        isLoading={true}
        title={messages.title}
        info={messages.info}
        img={myImg}
      />
    );
  }
}

export default ExportToGitHubOption;



