import {
  IExportSystemRequest,
  IExportSystemToFileSystemResponse,
  IExportSystemToGitHubResponse,
} from '../console-entity-models/exportSystem';
import { AllowedMethods, consoleFetch } from '../cbFetch';

export const exportToFileSystem = (
  param: IExportSystemRequest,
): Promise<IExportSystemToFileSystemResponse> =>
  consoleFetch('/console-api/batch/export/filesystem', {
    method: AllowedMethods.POST,
    body: JSON.stringify(param),
  });

export const exportToGitHub = (
  githubOauthAccessToken: string,
  body: IExportSystemRequest,
): Promise<IExportSystemToGitHubResponse> =>
  consoleFetch('/console-api/batch/export/github', {
    method: AllowedMethods.POST,
    body: JSON.stringify(body),
    headers: {
      githubOauthAccessToken,
    },
  });



