/**
 *
 * ExportOption
 *
 */

import React, { SFC } from 'react';
import HoverCard from 'components/HoverCard';
import { CardImg, CardBody, CardTitle, CardText } from 'reactstrap';
// import styled from 'styled-components';

import { FormattedMessage } from 'react-intl';

interface ExportOptionProps {
  title: FormattedMessage.MessageDescriptor;
  info: FormattedMessage.MessageDescriptor;
  img: string;
  isLoading?: boolean;
}

const ExportOption: SFC<ExportOptionProps> = (props) => {
  return (
    <HoverCard className="c-pointer">
      <CardImg id="hello" src={props.img} />
      <CardBody>
        <CardTitle>
          <FormattedMessage {...props.title} />
        </CardTitle>
        <CardText>
          <FormattedMessage {...props.info} />
        </CardText>
      </CardBody>
    </HoverCard>
  );
};

ExportOption.defaultProps = {
  isLoading: false,
};

export default ExportOption;



