/*
 *
 * SystemDetail
 *
 */

import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
import { Route, Link } from 'react-router-dom';

import { compose } from 'redux';

import { CBDispatch } from 'cb-utils/typeUtils';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import { systemSelectors } from '../Console/ducks';
// import { AppState } from 'reducers';
import ConsolePage from 'components/ConsolePage';
import { Container, Row, Button } from 'reactstrap';
import ConsolePageHeader from 'components/ConsolePageHeader';
import AssetClassCard from '../AssetClassCard';
import { EntityModels } from '../Console/state';
import assetMap from 'cb-utils/console-entity-models/assetClassInfo';
import { SettingsIcon } from 'components/EntityIcons';
import { createShowBaseModal } from '../Console/actions/ui';
import { ExportIcon } from 'components/Icons';
import ConsoleRoutes from '../Console/routes';
import { createSystemPageLink, overallUrls } from 'cb-utils/getUrls';
import ExportSystemModal from '../ExportSystemModal';
import { injectIntl, FormattedMessage } from 'react-intl';
import { createCreateOrInstallFromGitHubRequest } from '../Console/actions/ipm';
import CardColumn from './CardColumn';
import MessagingAnalyticsCard from './MessagingAnalyticsCard';
import { getPlatformInfo } from 'cb-utils/platformInfo';
import { createStructuredSelector } from 'reselect';
import messages from './messages';

const SettingsWrapper = styled(SettingsIcon)`
  margin-left: 0.5rem;
`;

interface SystemDetailProps extends SystemRouteProps<{}> {}

export class SystemDetail extends React.PureComponent<
  SystemDetailProps &
    IStateToProps &
    IDispatchToProps &
    ReactIntl.InjectedIntlProps,
  {}
> {
  render() {
    const { edgeName, systemKey } = this.props.match.params;
    const onEdge = edgeName || getPlatformInfo().isEdge;

    return (
      <ConsolePage subheader={this.renderSubHeader()}>
        <Helmet title={this.getName()} meta={[{ name: '', content: '' }]} />
        <Route
          path={ConsoleRoutes.EXPORT_SYSTEM_MODAL_CHOOSE_DESTINATION_TYPE}
          component={ExportSystemModal}
        />
        <Container>
          <Row>
            {this.createColumn('services')}
            {this.createColumn('libraries')}
            {this.createColumn('collections')}
            {!onEdge && this.createColumn('adaptors')}
            {this.createColumn('portals')}
            {this.createColumn('plugins')}
            {!onEdge && this.createColumn('deployments')}
            {this.createColumn('roles')}
            {this.createColumn('users')}
            {this.createColumn('devices')}
            {!onEdge && this.createColumn('edges')}
            <CardColumn>
              <Link
                to={overallUrls.messages({
                  systemKey,
                  edgeName: this.getEdgeName(),
                })}
              >
                <MessagingAnalyticsCard systemKey={systemKey} />
              </Link>
            </CardColumn>
          </Row>
        </Container>
      </ConsolePage>
    );
  }

  getName = () => (this.props.system ? this.props.system.name : '');

  getEdgeName = () =>
    this.props.match.params.edgeName &&
    this.props.match.params.edgeName.substring(1); // remove ':' in :myEdge - couldn't find right regex to remove in route

  createColumn = (assetClass: keyof EntityModels) => {
    const assetClassInfo = assetMap[assetClass];
    const { systemKey } = this.props.match.params;
    const edge = this.getEdgeName();

    const card = (
      <AssetClassCard
        edge={edge}
        assetClass={assetClass}
        systemKey={this.props.match.params.systemKey}
      />
    );
    return (
      <CardColumn>
        {assetClassInfo.link && (
          <Link
            to={createSystemPageLink(assetClassInfo.link, {
              systemKey,
              edgeName: edge,
            })}
          >
            {card}
          </Link>
        )}
      </CardColumn>
    );
  };

  renderSubHeader = () => {
    if (this.props.system) {
      const { edgeName } = this.props.match.params;
      const edge = edgeName && edgeName.substring(1); // remove ':' in :myEdge - couldn't find right regex to remove in route

      const systemName = this.props.system.name;
      return (
        <React.Fragment>
          <ConsolePageHeader
            header={
              <div>
                {edge || systemName}
                {!edge && (
                  <SettingsWrapper
                    onClick={this.props.openSettings}
                    className="c-pointer"
                    data-cy="system-settings-button"
                  />
                )}
              </div>
            }
            subHeader={<div>{edge ? systemName : ''}</div>}
            actionButton={() => (
              <Link to={`${this.props.match.url}/export`}>
                <Button color="primary">
                  <ExportIcon />
                  &nbsp;
                  <FormattedMessage {...messages.export} />
                </Button>
              </Link>
            )}
          />
        </React.Fragment>
      );
    }
    return null;
  };
}

const mapStateToProps = createStructuredSelector({
  system: systemSelectors.currentSystem(),
});

type IStateToProps = ReturnType<typeof mapStateToProps>;

function mapDispatchToProps(dispatch: CBDispatch) {
  return {
    openSettings: () =>
      dispatch(
        createShowBaseModal({
          component: 'SystemSettingsModal',
          props: {},
        }),
      ),
    createSystemFromGitHub: (id: string, githubUrl?: string) =>
      dispatch(
        createCreateOrInstallFromGitHubRequest(id, undefined, githubUrl, true),
      ),
  };
}
type IDispatchToProps = ReturnType<typeof mapDispatchToProps>;

const withConnect = connect(mapStateToProps, mapDispatchToProps);

export default compose(withConnect, injectIntl)(SystemDetail);



