/**
 *
 * ExportSystemSuccess
 *
 */

import React, { SFC } from 'react';
import styled from 'styled-components';

import { FormattedMessage } from 'react-intl';
import { ModalBody, ModalFooter, Button } from 'reactstrap';
import messages from './messages';
import {
  ExportSystemResponse,
  ExportSystemDestinationTypes,
  IExportSystemToFileSystemResponse,
  IExportSystemToGitHubResponse,
} from 'cb-utils/console-entity-models/exportSystem';
import { DownloadIcon } from '../Icons';
import consoleMessages from 'containers/Console/messages';
import {
  PUBLISH_PACKAGE_TO_IPM_URL,
  BROWSE_PACKAGES_ON_IPM_URL,
} from 'cb-utils/console-entity-models/ipm';

const IconContainer = styled.div`
  font-size: 8rem;
`;

interface ILinkInfo {
  href: string;
  message?: FormattedMessage.MessageDescriptor;
  text?: string;
}

const createFormattedMessage = (mess: FormattedMessage.MessageDescriptor) => (
  <FormattedMessage {...mess} />
);

const InfoSection = styled.div`
  margin-bottom: 3rem;
`;

const InfoWithLinkSection = (
  header: FormattedMessage.MessageDescriptor,
  link: ILinkInfo,
  subHeader?: FormattedMessage.MessageDescriptor,
) => (
  <InfoSection>
    {Header(header)}
    {subHeader && SubHeader(subHeader)}
    {DynamicLink(link)}
  </InfoSection>
);

const DynamicLink = (info: ILinkInfo) => (
  <a target="_blank" href={info.href}>
    {info.message && createFormattedMessage(info.message)}
    {info.text && info.text}
  </a>
);

const SubHeader = (message: FormattedMessage.MessageDescriptor) => (
  <h4 className="text-muted lead">{createFormattedMessage(message)}</h4>
);

const Header = (message: FormattedMessage.MessageDescriptor) => (
  <h1 className="display-4">{createFormattedMessage(message)}</h1>
);

const SuccessMessage = () => Header(messages.success);

interface ExportSystemSuccessProps {
  type: ExportSystemDestinationTypes;
  data: ExportSystemResponse;
  onClose: () => void;
}

const renderBody = (props: ExportSystemSuccessProps) => {
  switch (props.type) {
    case ExportSystemDestinationTypes.FILE_SYSTEM:
      return (
        <React.Fragment>
          <SuccessMessage />
          <a
            href={`/console-api/batch/export/files?fullFileName=${
              (props.data as IExportSystemToFileSystemResponse).zipAddress
            }`}
          >
            <IconContainer>
              <DownloadIcon />
            </IconContainer>
            <FormattedMessage {...messages.clickHereToDownload} />
          </a>
        </React.Fragment>
      );
    case ExportSystemDestinationTypes.GITHUB:
      const repoUrl = (props.data as IExportSystemToGitHubResponse).repoURL;
      return (
        <React.Fragment>
          {InfoWithLinkSection(
            messages.success,
            { href: repoUrl, text: repoUrl },
            messages.findPackageHere,
          )}
          {InfoWithLinkSection(
            messages.joinUs,
            {
              href: PUBLISH_PACKAGE_TO_IPM_URL,
              message: messages.publishIotPackageLinkText,
            },
            messages.joinUsDetail,
          )}
          {InfoWithLinkSection(messages.orBrowse, {
            href: BROWSE_PACKAGES_ON_IPM_URL,
            message: messages.browsePackages,
          })}
        </React.Fragment>
      );
  }
};

const ExportSystemSuccess: SFC<ExportSystemSuccessProps> = (props) => {
  return (
    <React.Fragment>
      <ModalBody>
        <div className="text-center">{renderBody(props)}</div>
      </ModalBody>
      <ModalFooter>
        <Button onClick={props.onClose}>
          <FormattedMessage {...consoleMessages.close} />
        </Button>
      </ModalFooter>
    </React.Fragment>
  );
};

export default ExportSystemSuccess;



