/*
 *
 * ExportSystemForm
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import injectSaga from 'utils/injectSaga';
import { ModalFooter, ModalBody, Button } from 'reactstrap';
import { Formik, Field, FormikProps, Form, FieldArray } from 'formik';
import Yup from 'yup';
import FormikInputWrapper, {
  FormikArrayInputWrapper,
} from 'components/FormikInputWrapper';
import { compose } from 'redux';
import injectReducer from 'utils/injectReducer';
import makeSelectExportSystemForm from './selectors';
import reducer from './reducer';
import { STORE_KEY } from './constants';
import saga from './sagas';
import { CBDispatch } from 'cb-utils/typeUtils';
import { SystemRouteProps } from 'cb-utils/systemRouteProps';
import { systemSelectors } from '../Console/ducks';
import PackageFilePicker from 'containers/PackageFilePicker';
import {
  createInitializeSystemExportRequest,
  createCleanUpExportSystem,
  createGetClientIdAndAccessTokenRequest,
} from '../Console/actions/ipm';
import Tabs from 'components/Tabs';
import consoleMessages from 'containers/Console/messages';
import { FormattedMessage, injectIntl } from 'react-intl';
import { SettingTypes } from 'utils/types';
import messages from './messages';
import {
  IExportSystemMetaData,
  ISystemMetaDataTagFields,
  ExportSystemDestinationTypes,
} from 'cb-utils/console-entity-models/exportSystem';
import CbCollapseArea from 'components/CbCollapseArea';
import CbButton, { isSubmitDisabled } from 'components/CbButton';
import { createExportSystemRequest, createOnAssetTypeChange } from './actions';
import { selectCurrentUser } from '../Console/selectors/currentUser';
import ExportSystemSuccess from 'components/ExportSystemSuccess';
import { GitHubAccessTokenKey } from 'cb-utils/console-entity-models/github';
import { createNavToOverview } from '../Console/actions/ui';
import { exportSystemBaseRoute } from '../Console/routes/exportSystemModal';
import { verifyGitHubAccessToken } from 'cb-utils/services/github';
import {
  urlContainsCodeParam,
  getCodeFromUrl,
  IpmAssetTypes,
} from 'cb-utils/console-entity-models/ipm';
import AssetTypePicker from 'components/AssetTypePicker';
import { createStructuredSelector } from 'reselect';

const AdvancedTags = () => <FormattedMessage {...messages.advancedTags} />;
const AssetTypes = () => <FormattedMessage {...messages.assetTypes} />;

enum MyTabs {
  ABOUT = 'about',
  ADVANCED = 'advanced',
}

interface ExportSystemFormProps
  extends SystemRouteProps<{ type: ExportSystemDestinationTypes }> {
  onCancel: () => void;
}

interface IState {
  currentTab: MyTabs;
  info: IExportSystemMetaData;
}

const formatTags = (keywords: string[]) => keywords.map((label) => ({ label }));

export class ExportSystemForm extends React.Component<
  ExportSystemFormProps &
    StateToProps &
    IDispatchToProps &
    ReactIntl.InjectedIntlProps,
  IState
> {
  state: IState = {
    currentTab: MyTabs.ABOUT,
    info: {
      name: this.props.systemInfo ? this.props.systemInfo.name : '',
      description: this.props.systemInfo
        ? this.props.systemInfo.description
        : '',
      asset_types: [],
      version: '1.0.0',
      keywords: ['clearblade', 'iot', 'platform'],
      prog_lang: [],
      clouds: [],
      things: [],
      domains: [],
      protocols: [],
      gateways: [],
      author: { email: this.props.currentUser.email },
      license: 'MIT',
      image_url:
        'https://docs.clearblade.com/v/3/static/img/default_package.png',
      private: false,
    },
  };

  componentWillMount() {
    if (this.props.match.params.type === ExportSystemDestinationTypes.GITHUB) {
      if (!localStorage[GitHubAccessTokenKey]) {
        if (urlContainsCodeParam()) {
          this.props.getClientIdAndAccessToken(getCodeFromUrl());
        } else {
          // no token and no access code? be gone with you!
          this.props.navBackToBeginningOfExport();
        }
      } else {
        verifyGitHubAccessToken(localStorage[GitHubAccessTokenKey]).catch(
          this.props.navBackToBeginningOfExport,
        );
      }
    }
  }

  render() {
    const formatMessage = this.props.intl.formatMessage;
    return this.props.ExportSystemForm.success ? (
      <ExportSystemSuccess
        type={this.props.match.params.type}
        data={this.props.ExportSystemForm.successResponse}
        onClose={this.props.onCancel}
      />
    ) : (
      <Formik
        validationSchema={Yup.object().shape({
          name: Yup.string()
            .required(formatMessage(consoleMessages.errorNameRequired))
            .matches(/^[^\s]+$/, formatMessage(consoleMessages.errorSpaces))
            .matches(
              /^[\w-]+$/,
              formatMessage(consoleMessages.errorSpecialCharacters),
            ),
        })}
        initialValues={this.state.info}
        onSubmit={(values: IExportSystemMetaData) => {
          this.props.onExport({
            ...values,
            asset_types: this.props.ExportSystemForm.assetTypes,
          });
        }}
        render={({
          values,
          touched,
          errors,
          dirty,
          submitForm,
        }: FormikProps<IExportSystemMetaData>) => {
          return (
            <React.Fragment>
              {/* going to use this modal for now...perhaps in the future we'll need to use something more specific */}
              <Form onSubmit={submitForm}>
                <ModalBody>
                  <Tabs
                    currentTab={this.state.currentTab}
                    selectNewTab={this.toggleTab}
                    tabs={[
                      {
                        id: MyTabs.ABOUT,
                        title: <FormattedMessage {...consoleMessages.about} />,
                        component: (
                          <React.Fragment>
                            <Field
                              name="name"
                              label={
                                <FormattedMessage {...consoleMessages.name} />
                              }
                              simpleHelp={messages.helpName}
                              type={SettingTypes.TEXT_TYPE}
                              component={FormikInputWrapper}
                              placeholder={formatMessage(consoleMessages.name)}
                            />
                            <Field
                              name="description"
                              label={
                                <FormattedMessage
                                  {...consoleMessages.description}
                                />
                              }
                              simpleHelp={messages.helpDescription}
                              type={SettingTypes.TEXT_AREA_TYPE}
                              component={FormikInputWrapper}
                              placeholder={formatMessage(
                                consoleMessages.description,
                              )}
                            />
                            <Field
                              name="image_url"
                              label={
                                <FormattedMessage {...messages.imageUrl} />
                              }
                              simpleHelp={messages.helpImage}
                              type={SettingTypes.IMAGE_URL_INPUT}
                              component={FormikInputWrapper}
                              isFakeEvent
                            />
                            <Field
                              name="version"
                              label={<FormattedMessage {...messages.version} />}
                              type={SettingTypes.TEXT_TYPE}
                              component={FormikInputWrapper}
                            />
                            <Field
                              name="license"
                              label={<FormattedMessage {...messages.license} />}
                              simpleHelp={messages.helpLicense}
                              type={SettingTypes.TEXT_TYPE}
                              component={FormikInputWrapper}
                            />
                            <FieldArray
                              name="keywords"
                              render={({ ...rest }) => {
                                return (
                                  <FormikArrayInputWrapper
                                    // @ts-ignore - Clark needs to fix this
                                    {...rest}
                                    name="keywords"
                                    type={SettingTypes.TAG_INPUT}
                                    label={
                                      <FormattedMessage
                                        {...messages.keywords}
                                      />
                                    }
                                    simpleHelp={messages.helpKeywords}
                                    formatValue={formatTags}
                                  />
                                );
                              }}
                            />

                            <CbCollapseArea
                              showText={<AdvancedTags />}
                              hideText={<AdvancedTags />}
                            >
                              <FieldArray
                                name="protocols"
                                render={({ ...rest }) => {
                                  return (
                                    <FormikArrayInputWrapper
                                      // @ts-ignore - Clark needs to fix this
                                      {...rest}
                                      name="protocols"
                                      type={SettingTypes.TAG_INPUT}
                                      label={
                                        <FormattedMessage
                                          {...messages.protocols}
                                        />
                                      }
                                      formatValue={formatTags}
                                    />
                                  );
                                }}
                              />
                              <FieldArray
                                name="things"
                                render={({ ...rest }) => {
                                  return (
                                    <FormikArrayInputWrapper
                                      // @ts-ignore - Clark needs to fix this
                                      {...rest}
                                      name="things"
                                      type={SettingTypes.TAG_INPUT}
                                      label={
                                        <FormattedMessage
                                          {...messages.things}
                                        />
                                      }
                                      formatValue={formatTags}
                                    />
                                  );
                                }}
                              />
                              <FieldArray
                                name="domains"
                                render={({ ...rest }) => {
                                  return (
                                    <FormikArrayInputWrapper
                                      // @ts-ignore - Clark needs to fix this
                                      name="domains"
                                      {...rest}
                                      type={SettingTypes.TAG_INPUT}
                                      label={
                                        <FormattedMessage
                                          {...messages.domains}
                                        />
                                      }
                                      formatValue={formatTags}
                                    />
                                  );
                                }}
                              />
                              <FieldArray
                                name="gateways"
                                render={({ ...rest }) => {
                                  return (
                                    <FormikArrayInputWrapper
                                      // @ts-ignore - Clark needs to fix this
                                      {...rest}
                                      name="gateways"
                                      type={SettingTypes.TAG_INPUT}
                                      label={
                                        <FormattedMessage
                                          {...messages.gateways}
                                        />
                                      }
                                      formatValue={formatTags}
                                    />
                                  );
                                }}
                              />
                              <FieldArray
                                name="prog_lang"
                                render={({ ...rest }) => {
                                  return (
                                    <FormikArrayInputWrapper
                                      // @ts-ignore - Clark needs to fix this
                                      {...rest}
                                      name="prog_lang"
                                      type={SettingTypes.TAG_INPUT}
                                      label={
                                        <FormattedMessage
                                          {...messages.prog_lang}
                                        />
                                      }
                                      formatValue={formatTags}
                                    />
                                  );
                                }}
                              />
                              <FieldArray
                                name="clouds"
                                render={({ ...rest }) => {
                                  return (
                                    <FormikArrayInputWrapper
                                      // @ts-ignore - Clark needs to fix this
                                      {...rest}
                                      name="clouds"
                                      type={SettingTypes.TAG_INPUT}
                                      label={
                                        <FormattedMessage
                                          {...messages.clouds}
                                        />
                                      }
                                      formatValue={formatTags}
                                    />
                                  );
                                }}
                              />
                            </CbCollapseArea>
                            <CbCollapseArea
                              showText={<AssetTypes />}
                              hideText={<AssetTypes />}
                            >
                              <AssetTypePicker
                                selectedTypes={
                                  this.props.ExportSystemForm.assetTypes
                                }
                                onChange={this.props.onAssetTypeChange}
                              />
                            </CbCollapseArea>
                          </React.Fragment>
                        ),
                      },
                      {
                        id: MyTabs.ADVANCED,
                        title: (
                          <FormattedMessage {...consoleMessages.advanced} />
                        ),
                        component: (
                          <PackageFilePicker
                            id={this.props.systemInfo.name}
                            fetchAction={createInitializeSystemExportRequest}
                          />
                        ),
                      },
                    ]}
                  />
                </ModalBody>
                <ModalFooter>
                  <Button onClick={this.props.onCancel}>
                    <FormattedMessage {...consoleMessages.cancel} />
                  </Button>
                  <CbButton
                    data-cy="export-system-submit-button"
                    disabled={isSubmitDisabled({ errors, touched })}
                    isLoading={this.props.ExportSystemForm.isExporting}
                    onClick={submitForm}
                    color="primary"
                  >
                    <FormattedMessage {...messages.export} />
                  </CbButton>
                </ModalFooter>
              </Form>
            </React.Fragment>
          );
        }}
      />
    );
  }

  handleTagRemoval = (key: keyof ISystemMetaDataTagFields, t: string) => {
    this.setState({
      info: {
        ...this.state.info,
        [key]: this.state.info[key].filter((f) => f !== t),
      },
    });
  };

  handleTagAddition = (key: keyof ISystemMetaDataTagFields, t: string) => {
    this.setState({
      info: {
        ...this.state.info,
        [key]: [...this.state.info[key], t],
      },
    });
  };

  toggleTab = (name: string) => {
    this.setState({
      currentTab: name as MyTabs,
    });
  };

  componentWillUnmount() {
    this.props.cleanUp();
  }
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  ExportSystemForm: makeSelectExportSystemForm(),
  systemInfo: systemSelectors.currentSystem(),
  currentUser: selectCurrentUser(),
});

function mapDispatchToProps(
  dispatch: CBDispatch,
  ownProps: ExportSystemFormProps,
) {
  return {
    onAssetTypeChange: (a: IpmAssetTypes[]) =>
      dispatch(createOnAssetTypeChange(a)),
    getClientIdAndAccessToken: (code: string) =>
      dispatch(createGetClientIdAndAccessTokenRequest(code)),
    cleanUp: () => dispatch(createCleanUpExportSystem()),
    onExport: (info: IExportSystemMetaData) =>
      dispatch(
        createExportSystemRequest(
          ownProps.match.params.type as ExportSystemDestinationTypes,
          info,
        ),
      ),
    navBackToBeginningOfExport: () =>
      dispatch(createNavToOverview(exportSystemBaseRoute)),
  };
}

type IDispatchToProps = ReturnType<typeof mapDispatchToProps>;

const withConnect = connect(mapStateToProps, mapDispatchToProps);

const withReducer = injectReducer({ key: STORE_KEY, reducer });
const withSaga = injectSaga({ key: STORE_KEY, saga });
export default compose(withReducer, withSaga, withConnect)(
  injectIntl(ExportSystemForm),
) as React.ComponentType<ExportSystemFormProps>;



