/**
 *
 * AssetTypePicker
 *
 */

import React from 'react';
// import styled from 'styled-components';

// import { FormattedMessage } from 'react-intl';
// import messages from './messages';
import { IpmAssetTypes } from 'cb-utils/console-entity-models/ipm';
import CbInputField from 'components/CbInputField';
import { SettingTypes, CBEvent } from 'utils/types';

interface AssetTypePickerProps {
  selectedTypes: IpmAssetTypes[];
  onChange: (a: IpmAssetTypes[]) => void;
}

class AssetTypePicker extends React.Component<AssetTypePickerProps, {}> {
  render() {
    return (
      <div>
        {this.createCheckbox(IpmAssetTypes.CODE_SERVICES)}
        {this.createCheckbox(IpmAssetTypes.CODE_LIBRARIES)}
        {this.createCheckbox(IpmAssetTypes.PORTALS)}
        {this.createCheckbox(IpmAssetTypes.COLLECTIONS)}
        {this.createCheckbox(IpmAssetTypes.TRIGGERS)}
        {this.createCheckbox(IpmAssetTypes.TIMERS)}
        {this.createCheckbox(IpmAssetTypes.ADAPTERS)}
        {this.createCheckbox(IpmAssetTypes.ROLES)}
        {this.createCheckbox(IpmAssetTypes.PLUGINS)}
        {this.createCheckbox(IpmAssetTypes.USERS)}
        {this.createCheckbox(IpmAssetTypes.EDGES)}
        {this.createCheckbox(IpmAssetTypes.DEVICES)}
      </div>
    );
  }

  onChange = (name: IpmAssetTypes, val: boolean) => {
    let rtn = this.props.selectedTypes;
    const index = this.props.selectedTypes.indexOf(name);
    if (val) {
      rtn = [...rtn, name];
    } else {
      rtn = [...rtn.slice(0, index), ...rtn.slice(index + 1)];
    }
    this.props.onChange(rtn);
  };

  createCheckbox = (name: IpmAssetTypes) => {
    return (
      <CbInputField
        type={SettingTypes.BOOLEAN_TYPE}
        name={name}
        display_name={name}
        value={this.props.selectedTypes.indexOf(name) > -1}
        labelText={name}
        onChange={(e: CBEvent) => this.onChange(name, e.target.value)}
      />
    );
  };
}

export default AssetTypePicker;



