import React from 'react';
import styled from 'styled-components';
import { Col } from 'reactstrap';

const ColWrapper = styled(Col)`
  a:hover {
    text-decoration: none; /* need to override the default decoration that's set by anchor tags */
  }
`;

const CardColumn = (props: React.Props<{}>) => {
  return (
    <ColWrapper sm="6" md="4" lg="3" xl="2">
      {props.children}
    </ColWrapper>
  );
};

export default CardColumn;



