import React from 'react';
import AssetClassCardPrimitive from 'containers/AssetClassCard/AssetClassCardPrimitive';
import { getEventTotals } from 'cb-utils/services/analytics';
import { MessagesIcon } from 'components/EntityIcons';
import consoleMessages from 'containers/Console/messages';
import messages from './messages';
import { injectIntl } from 'react-intl';

interface IProps {
  systemKey: string;
}

interface IState {
  isLoading: boolean;
  count: number;
}

const MILLISECOND_TO_SECOND_MULTIPLIER = 1000;
const SECONDS_IN_AN_HOUR = 3600;

class MessagingAnalyticsCard extends React.Component<
  IProps & ReactIntl.InjectedIntlProps,
  IState
> {
  state: IState = {
    isLoading: true,
    count: null,
  };

  componentDidMount() {
    const anHourAgo = Math.round(
      Date.now() / MILLISECOND_TO_SECOND_MULTIPLIER - SECONDS_IN_AN_HOUR,
    );
    getEventTotals({
      scope: {
        system: this.props.systemKey,
      },
      filter: {
        module: 'messaging',
        action: 'publish',
        interval: SECONDS_IN_AN_HOUR,
        range: {
          start: anHourAgo,
        },
      },
    })
      .then((data) => {
        this.setState({
          count: data.values[0] && data.values[0].count,
        });
      })
      .finally(() => this.setState({ isLoading: false }));
  }

  render() {
    return (
      <AssetClassCardPrimitive
        icon={MessagesIcon}
        msg={consoleMessages.assetClassMessages}
        isFetching={this.state.isLoading}
        text={
          this.state.count &&
          this.props.intl.formatMessage(messages.messagesInLastHour, {
            number: this.state.count,
          })
        }
      />
    );
  }
}

export default injectIntl(MessagingAnalyticsCard);



