import { all, takeEvery, select, put, call } from 'redux-saga/effects';
import uuid from 'uuid/v4';
import { ActionConstants } from './actionConstants';
import { ActionConstants as IpmConstants } from 'containers/Console/actions/ipmConstants';
import {
  ExportSystemRequest,
  createExportSystemError,
  createExportSystemSuccess,
  createInitExportSystemFormSuccess,
  ExportSystemError,
} from './actions';
import {
  IExportSystemRequest,
  ExportSystemDestinationTypes,
  IExportSystemMetaData,
  ExportSystemResponse,
} from 'cb-utils/console-entity-models/exportSystem';
import { SystemModel } from 'cb-utils/console-entity-models';
import { systemSelectors } from '../Console/ducks';
import {
  selectPackageStructureById,
  selectPackageAssetSelectionsById,
} from '../Console/selectors/ipm';
import {
  IPackageWithStructureInfo,
  formatListOfAssetsForImportOrExport,
  IPackageAssetNode,
  IpmAssetTypes,
  AssetTypeToDirectoryMap,
} from 'cb-utils/console-entity-models/ipm';
import {
  exportToFileSystem,
  exportToGitHub,
} from 'cb-utils/services/exportSystem';
import { GitHubAccessTokenKey } from 'cb-utils/console-entity-models/github';
import {
  createNavToOverview,
  createOnConsoleErrorModal,
} from '../Console/actions/ui';
import { exportSystemBaseRoute } from '../Console/routes/exportSystemModal';
import { GetPackageStructureSuccess } from '../Console/actions/ipm';
import messages from './messages';
import { formatError } from 'cb-utils/formatError';

const compileTags = (meta: IExportSystemMetaData) => [
  ...meta.keywords,
  ...meta.clouds,
  ...meta.domains,
  ...meta.things,
  ...meta.gateways,
  ...meta.prog_lang,
  ...meta.protocols,
];

// Individual exports for testing
export function* exportSystemSaga() {
  yield takeEvery(ActionConstants.EXPORT_SYSTEM_REQUEST, function*(
    action: ExportSystemRequest,
  ) {
    try {
      const exportType = action.payload.type;
      const systemName = action.payload.metadata.name;
      const systemInfo: SystemModel = yield select(
        systemSelectors.currentSystem(),
      );
      const currentSystemName = systemInfo.name;
      const structure: IPackageWithStructureInfo = yield select(
        selectPackageStructureById(currentSystemName),
      );
      const assetSelections: string[] = yield select(
        selectPackageAssetSelectionsById(currentSystemName),
      );

      const payload: IExportSystemRequest = {
        developerEmail: action.payload.metadata.author.email,
        listOfAssets: formatListOfAssetsForImportOrExport(
          structure.data.tree,
          assetSelections,
        ),
        refSystemName: systemName,
        repoName:
          exportType === ExportSystemDestinationTypes.FILE_SYSTEM
            ? 'local'
            : systemName,
        systemDescription: action.payload.metadata.description,
        refDirName: structure.data.sha,
        tags: compileTags(action.payload.metadata),
        metadata: action.payload.metadata,
      };

      let resp: ExportSystemResponse;
      if (exportType === ExportSystemDestinationTypes.FILE_SYSTEM) {
        resp = yield call(exportToFileSystem, payload);
      } else if (exportType === ExportSystemDestinationTypes.GITHUB) {
        if (localStorage[GitHubAccessTokenKey]) {
          resp = yield call(
            exportToGitHub,
            localStorage[GitHubAccessTokenKey],
            payload,
          );
        } else {
          throw new Error('No GitHub token');
        }
      } else {
        throw new Error('Export type not supported');
      }

      yield put(createExportSystemSuccess(resp));
    } catch (e) {
      yield put(createExportSystemError(e));
    }
  });
}

function* getTokenErrorSaga() {
  yield takeEvery(
    IpmConstants.GET_CLIENT_ID_AND_ACCESS_TOKEN_ERROR,
    function*() {
      yield put(createNavToOverview(exportSystemBaseRoute));
    },
  );
}

const createListOfSelectedAssetTypes = (
  allFiles: IPackageAssetNode[],
): IpmAssetTypes[] => {
  const rtn: IpmAssetTypes[] = [];
  allFiles.forEach((f) => {
    for (const key in AssetTypeToDirectoryMap) {
      if (
        f.path === AssetTypeToDirectoryMap[key] &&
        rtn.indexOf(key as IpmAssetTypes) === -1
      ) {
        rtn.push(key as IpmAssetTypes);
      }
    }
  });
  return rtn;
};

function* initFormSaga() {
  yield takeEvery(IpmConstants.GET_PACKAGE_STRUCTURE_SUCCESS, function*(
    action: GetPackageStructureSuccess,
  ) {
    const assetTypes = createListOfSelectedAssetTypes(action.payload.resp.tree);
    yield put(createInitExportSystemFormSuccess({ assetTypes }));
  });
}

function* exportSystemErrorSaga() {
  yield takeEvery(ActionConstants.EXPORT_SYSTEM_ERROR, function*(
    action: ExportSystemError,
  ) {
    yield put(
      createOnConsoleErrorModal({
        id: uuid(),
        title: messages.exportError,
        body: formatError(action.payload.error),
      }),
    );
  });
}

// All sagas to be loaded
export default function*() {
  yield all([
    exportSystemSaga(),
    getTokenErrorSaga(),
    initFormSaga(),
    exportSystemErrorSaga(),
  ]);
}



