/*
 *
 * ExportSystemForm actions
 *
 */

import { BaseAction } from 'cb-utils/baseAction';
import { ActionConstants } from './actionConstants';
import {
  IExportSystemMetaData,
  ExportSystemDestinationTypes,
  ExportSystemResponse,
} from 'cb-utils/console-entity-models/exportSystem';
import { IpmAssetTypes } from 'cb-utils/console-entity-models/ipm';

export interface InitExportSystemFormSuccess extends BaseAction {
  type: ActionConstants.INIT_EXPORT_SYSTEM_FORM_SUCCESS;
  payload: {
    assetTypes: IpmAssetTypes[];
  };
}

export const createInitExportSystemFormSuccess = (payload: {
  assetTypes: IpmAssetTypes[];
}): InitExportSystemFormSuccess => ({
  type: ActionConstants.INIT_EXPORT_SYSTEM_FORM_SUCCESS,
  payload,
});

export interface OnAssetTypeChange extends BaseAction {
  type: ActionConstants.ON_ASSET_TYPE_CHANGE;
  payload: {
    assetTypes: IpmAssetTypes[];
  };
}

export const createOnAssetTypeChange = (
  assetTypes: IpmAssetTypes[],
): OnAssetTypeChange => ({
  type: ActionConstants.ON_ASSET_TYPE_CHANGE,
  payload: {
    assetTypes,
  },
});

export interface ExportSystemRequest extends BaseAction {
  type: ActionConstants.EXPORT_SYSTEM_REQUEST;
  payload: {
    metadata: IExportSystemMetaData;
    type: ExportSystemDestinationTypes;
  };
}

export const createExportSystemRequest = (
  type: ExportSystemDestinationTypes,
  metadata: IExportSystemMetaData,
): ExportSystemRequest => ({
  type: ActionConstants.EXPORT_SYSTEM_REQUEST,
  payload: {
    type,
    metadata,
  },
});

export interface ExportSystemSuccess extends BaseAction {
  type: ActionConstants.EXPORT_SYSTEM_SUCCESS;
  payload: {
    data: ExportSystemResponse;
  };
}

export const createExportSystemSuccess = (
  data: ExportSystemResponse,
): ExportSystemSuccess => ({
  type: ActionConstants.EXPORT_SYSTEM_SUCCESS,
  payload: {
    data,
  },
});

export interface ExportSystemError extends BaseAction {
  type: ActionConstants.EXPORT_SYSTEM_ERROR;
  payload: {
    error: any;
  };
}

export const createExportSystemError = (error: any): ExportSystemError => ({
  type: ActionConstants.EXPORT_SYSTEM_ERROR,
  payload: {
    error,
  },
});

export type ActionTypes =
  | InitExportSystemFormSuccess
  | OnAssetTypeChange
  | ExportSystemRequest
  | ExportSystemSuccess
  | ExportSystemError;



