import { IpmAssetTypes } from './ipm';

export enum ExportSystemDestinationTypes {
  GITHUB = 'github',
  FILE_SYSTEM = 'file_system',
}

export interface ISystemMetaDataTagFields {
  keywords: string[];
  prog_lang: string[];
  clouds: string[];
  things: string[];
  domains: string[];
  protocols: string[];
  gateways: string[];
}

export interface IExportSystemMetaData extends ISystemMetaDataTagFields {
  name: string;
  version: string;
  description: string;
  author: { email: string };
  license: string;
  image_url: string;
  private: boolean;
  asset_types: IpmAssetTypes[];
}

export interface IExportSystemRequest {
  developerEmail: string;
  listOfAssets: string[];
  refSystemName: string;
  repoName: string; // name of the new GitHub repo should be set to "local" for filesystem exports
  systemDescription: string; // note: this field is made redundant by metadata.description
  refDirName: string; // note: this field should be set to whatever is returned by IInitializeExportSystemResponse.dirName
  tags: string[]; // note: this field is a compilation of all the ISystemMetaDataTagFields
  metadata: IExportSystemMetaData;
}

export type ExportSystemResponse =
  | IExportSystemToFileSystemResponse
  | IExportSystemToGitHubResponse;

export interface IExportSystemToFileSystemResponse {
  zipAddress: string;
}

export interface IExportSystemToGitHubResponse {
  githubUsername: string;
  repoName: string;
  repoURL: string;
}

export interface IInitializeExportSystemRequestParams {
  devEmail: string;
  systemName: string;
}

export interface IInitializeExportSystemResponse {
  dirName: string;
  listOfFiles: string[];
}



