/*
 *
 * AssetClassCard
 *
 */

import React from 'react';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { CBDispatch } from 'cb-utils/typeUtils';

import { EntityModels } from '../Console/state';
import { AppState } from 'reducers';
import assetMap from 'cb-utils/console-entity-models/assetClassInfo';
import AssetClassCardPrimitive from './AssetClassCardPrimitive';
import { createStructuredSelector } from 'reselect';

const CardWrapper = styled(AssetClassCardPrimitive)`
  margin-bottom: 1rem;
`;

interface AssetClassCardProps {
  assetClass: keyof EntityModels;
  systemKey: string;
  className?: string;
  edge?: string;
}

type IMasterProps = AssetClassCardProps & DispatchToProps & StateToProps;

export class AssetClassCard extends React.PureComponent<IMasterProps, {}> {
  myIcon = assetMap[this.props.assetClass].icon;
  myMessage = assetMap[this.props.assetClass].message;

  componentWillMount() {
    this.props.init();
  }

  componentWillReceiveProps(nextProps: IMasterProps) {
    if (this.props.edge !== nextProps.edge) {
      // refetch if we've changed off edge (breadcrumbs from edge detail to system detail)
      this.props.init();
    }
  }

  render() {
    const { isFetching, count } = this.props;
    return (
      <CardWrapper
        className={this.props.className ? this.props.className : ''}
        isFetching={isFetching}
        text={count}
        icon={this.myIcon}
        msg={this.myMessage}
      />
    );
  }
}

type StateToProps = ReturnType<ReturnType<typeof mapStateToProps>>;

const mapStateToProps = (state: AppState, ownProps: AssetClassCardProps) =>
  createStructuredSelector({
    count: assetMap[ownProps.assetClass].selector,
    isFetching: assetMap[ownProps.assetClass].isFetching,
  });

const mapDispatchToProps = (
  dispatch: CBDispatch,
  ownProps: AssetClassCardProps,
) => ({
  init: () => {
    dispatch(assetMap[ownProps.assetClass].action(ownProps.systemKey));
  },
});
type DispatchToProps = ReturnType<typeof mapDispatchToProps>;

export default connect<StateToProps, DispatchToProps, AssetClassCardProps>(
  mapStateToProps,
  mapDispatchToProps,
)(AssetClassCard);



