import React from 'react';
import { FormattedMessage } from 'react-intl';
import HoverCard from 'components/HoverCard';
import { CardBody } from 'reactstrap';
import styled from 'styled-components';
import IconWrapper from 'components/AssetClassIconWrapper';

const CardBodyContainer = styled.div`
  cursor: pointer;
  height: 150px;
  display: flex;
  color: black;
`;

const MyCardBody = styled(CardBody)`
  width: 100%;
`;

interface IProps {
  icon: any;
  msg: FormattedMessage.MessageDescriptor;
  isFetching: boolean;
  text: string | number;
  className?: string;
}

const AssetClassCardPrimitive: React.SFC<IProps> = (props) => {
  const { isFetching, text, icon: AssetClassIcon, msg } = props;
  return (
    <HoverCard className={props.className ? props.className : ''}>
      <CardBodyContainer>
        <MyCardBody className="text-center">
          <IconWrapper isFetching={isFetching}>
            <AssetClassIcon />
          </IconWrapper>
          <h5 className="text-truncate">
            <FormattedMessage {...msg} />
          </h5>
          {!isFetching && (
            <div className="text-truncate">
              <sub>{text}</sub>
            </div>
          )}
        </MyCardBody>
      </CardBodyContainer>
    </HoverCard>
  );
};

export default AssetClassCardPrimitive;



